.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@media (min-width: 900px) {
  .window {
    border: solid 3px rgb(124, 121, 121);
    border-radius: 2%;
    width: 100vh;
    height: 60vh;
    margin: 3vh 0vh;
  }
}
.window {
  font-size: calc(15px + 3vmin);
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
Button {
  border: none;
  font-size: calc(10px + 2vmin);
  color: white;
  background-color: inherit;
}

Button:hover {
  color:rgb(124, 121, 121)
}
#time_left {
  font-size: calc(35px + 4vmin);
}
#lengths {
  font-size: calc(5px + 2vmin);
  display: flex;
  flex-direction: row;
  column-gap: 100px;
}
#timer {
  border: solid 3px rgb(124, 121, 121);
  border-radius: 2%;
  padding: 10px;
}
#sideways {
  display: flex;
  flex-direction: row;
}